<template>
  
  <div id="app" :class="this.$router.currentRoute.name" class="fadeIn animated">
    
      <router-view/>

  </div>
  
</template>

<script>

export default {

  methods: {

    // Register service worker.
    swRegister() {

      if ('serviceWorker' in navigator) {
        window.addEventListener('load', () => {
          navigator.serviceWorker.register('/service-worker.js');
        });
      }
    }

  },

  // Main lifecycle part.
  mounted() {

    // Register the service worker.
    this.swRegister();
  }
}

</script>

